$slideEdge: #68bebc;

.scm, .story, .molecules, .products {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 20px;
}
.story, .video-carousel {
  background:  url("../../images/brushsteel.jpg") no-repeat center;
}
.scm {
  .info {
    padding-left: 8.333333%;

    p{
      width: 50%;
    }
  }
  .moleculePattern {
    width: 100%;
  }
}

.story {
  img {
    width: 25%;
    height: 25%;

  }
  .placard {
    text-align: center;
    padding: 4rem 5rem;
    width: 50%;
    margin: 5rem auto;
    background-color: rgba(140, 205, 197, .06);
    border-top: 2px solid darken($slideEdge, 20%);
    border-right: 1px solid $slideEdge;
    border-bottom: 3px solid darken($slideEdge, 15%);
    border-left: 1px solid $slideEdge;
    box-shadow: 2px 4px 12px 2px darken($slideEdge, 50%);
  }
  .chemical {
    width: 30%;
    padding: 2rem;
  }
}

.molecules {
  flex-wrap: wrap;

  .title {
    width: 50%;
  }
  .traits {
    flex-direction: column;
    
    .trait {
      width: 30%;
      padding: 2rem;
    }
  }

  .microscope {
    width: 30%;
  }
  .drops {
    width: 30%;
    // margin-top: 90px;
    // img {
    //   float: right;
    // }
  }
}
.video-carousel {
  .video {
    max-width: 1068px;
    height: 300px;
    margin: 0 auto;
    background: url('../../images/scm-video.jpg');
    background-repeat: no-repeat;
    background-size: 80% 80%;
  }
  .carousel {
    text-align: center;
  }
}
.products {
  margin-top: -50px;

  .info {
    padding-top: 5rem;
    padding-left: 8.333333%;
  }
  .products-img {
    width: 100%;
  }
}
